body {
  font-family: OpenSans-Regular, Arial, sans-serif;
  font-size: 16px;
  color: #1a1a1a;
}

a {
  text-decoration: none;
  color: #1a1a1a;
}

h1 {
  font-size: 50px;
  font-family: Panton-SemiBold, Arial, sans-serif;
}

h2 {
  font-size: 35px;
  font-family: Panton-SemiBold, Arial, sans-serif;
}

h3 {
  font-size: 30px;
  font-family: Panton-SemiBold, Arial, sans-serif;
}

h4 {
  font-size: 20px;
  font-family: Panton-SemiBold, Arial, sans-serif;
}

.container {
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 40px;
}

.block {
  display: flex;
}

.footer {
  background-color: #fafafa;
}

.footer__logo {
  width: 144px;
  height: 118px;
}

@media only screen and (max-width: 767px) {
  .footer__logo {
    display: none;
  }
}

.footer__main {
  padding: 71px 0 24px 0;
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 767px) {
  .footer__main {
    padding: 50px 0 49px;
  }
}

.footer__copyrights {
  background-color: #eef1f5;
  padding: 23px 0 24px;
  padding: 23px 0 14px;
}

.footer__nav {
  width: 910px;
  margin-left: 20px;
}

@media only screen and (max-width: 425px) {
  .footer__nav {
    margin-left: 0;
  }
}

.footer__nav .nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1023px) {
  .footer__nav .nav {
    flex-wrap: wrap;
  }
}

@media only screen and (max-width: 767px) {
  .footer__nav .nav {
    flex-direction: column;
  }
}

.footer__nav .nav__col {
  padding: 0 10px;
}

@media only screen and (max-width: 1023px) {
  .footer__nav .nav__col {
    width: 50%;
  }
}

@media only screen and (max-width: 767px) {
  .footer__nav .nav__col {
    width: 100%;
    padding: 0;
  }
}

.footer__nav .nav__title {
  margin-bottom: 5px;
  font-family: Panton-Bold, Arial, sans-serif;
}

@media only screen and (max-width: 767px) {
  .footer__nav .nav__title {
    font-size: 16px;
    display: none;
  }
}

.footer__nav .nav__title--mob {
  white-space: nowrap;
  display: none;
  position: relative;
  padding-right: 20px;
}

.footer__nav .nav__title--mob:after {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  content: '';
  background-image: url("../img/arrow-sm.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 16px;
  width: 16px;
}

@media only screen and (max-width: 767px) {
  .footer__nav .nav__title--mob {
    display: inline-block;
  }
}

.footer__nav .nav__items {
  margin-bottom: 40px;
}

@media only screen and (max-width: 767px) {
  .footer__nav .nav__items {
    display: none;
  }
}

.footer__nav .nav__item {
  line-height: 29px;
  margin-bottom: 1px;
}

.footer__nav .nav__link {
  font-size: 14px;
  font-family: OpenSans-Light, Arial, sans-serif;
}

.footer__nav .nav__more-link {
  font-size: 14px;
  color: #14c1e5;
  font-family: OpenSans-Light, Arial, sans-serif;
  position: relative;
}

.footer__nav .nav__more-link:before {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  content: '';
  background-image: url("../img/arrow-sm.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 16px;
  width: 16px;
}

.footer__nav .nav__more-link span {
  margin-left: 20px;
}

.copyrights {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.copyrights__text {
  color: #8593a4;
  font-size: 15px;
  margin-bottom: 10px;
  margin-right: 20px;
}

@media only screen and (max-width: 767px) {
  .copyrights__text {
    display: none;
  }
}

.copyrights__links {
  display: flex;
  margin-bottom: 10px;
}

@media only screen and (max-width: 619px) {
  .copyrights__links {
    justify-content: center;
    flex-wrap: wrap;
  }
}

.copyrights__links li {
  padding-left: 20px;
  white-space: nowrap;
}

@media only screen and (max-width: 619px) {
  .copyrights__links li {
    margin-top: 10px;
  }
}

.copyrights__links li a {
  font-size: 15px;
  color: #14c1e5;
}

.copyrights__links li:not(:first-child) {
  padding-left: 20px;
}

@media only screen and (max-width: 619px) {
  .copyrights__links li:not(:first-child) {
    border-right: 0;
  }
}

.copyrights__links li:not(:last-child) {
  border-right: 1px solid #b8b8b8;
  padding-right: 20px;
}
