@include font(OpenSans-Regular, OpenSans-Regular);

@include font(OpenSans-SemiBold, OpenSans-SemiBold);

@include font(OpenSans-Light, OpenSans-Light);

@include font(Panton-Black, Panton-Black);

@include font(Panton-Bold, Panton-Bold);

@include font(Panton-ExtraBold, Panton-ExtraBold);

@include font(Panton-Regular, Panton-Regular);

@include font(Panton-SemiBold, Panton-SemiBold);

@mixin OSReg {
  font-family: OpenSans-Regular, Arial, sans-serif; }

@mixin OSLight {
  font-family: OpenSans-Light, Arial, sans-serif; }

@mixin OSSemi {
  font-family: OpenSans-SemiBold, Arial, sans-serif; }

@mixin PBlack {
  font-family: Panton-Black, Arial, sans-serif; }

@mixin PBold {
  font-family: Panton-Bold, Arial, sans-serif; }

@mixin PExtra {
  font-family: Panton-ExtraBold, Arial, sans-serif; }

@mixin PReg {
  font-family: Panton-Regular, Arial, sans-serif; }

@mixin PSemi {
  font-family: Panton-SemiBold, Arial, sans-serif; }


